@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* form layout */
.form-row, .input-wrap {
	position: relative; // Важно! Для позицирования отметки подтверждения полей формы
	margin-bottom: $form-bottom-space;
	z-index: 1; } // Важно! Отметка подтверждения полей формы имеет z-index: -1

.form-row:last-child {
	margin-bottom: 0; }

.input-holder {
	position: relative; // Важно! Для позицирования отметки подтверждения полей формы
	z-index: 1; } // Важно! Отметка подтверждения полей формы имеет z-index: -1

.label-holder, label {
	@include font-normal;
	@include font($font-size, $line-height);
	color: $text-color-light;
	margin-bottom: 7px; }

label, .label {
	@include display-inline-block-top;
	user-select: none; }

.label-holder {
	label, .label {
		margin-bottom: 0; } }

.form-mark {
	@include display-inline-block-top;
	position: relative;
	margin-left: -15px;
	left: 15px;
	color: $mark-color;

	.form-note & {
		margin-left: 0;
		left: auto; } }

.form-row {
	@include margin-horizontal(-#{round($form-gap*0.5)});
	font-size: 0;
	letter-spacing: -4px;

	.input-wrap {
		@include display-inline-block-top;
		width: calc(100% * 0.5 - #{$form-gap});
		@include margin-horizontal(#{round($form-gap*0.5)});
		margin-bottom: 0;

		font-size: $font-size;
		letter-spacing: normal;

		@include media('<mob-lg') {
			display: block;
			width: auto;
			margin-bottom: $form-bottom-space--sm; } }

	label {
		@include truncate(100%); } }

.form-columnize {
	column-count: 2; }

.form-columnize--3 {
	column-count: 3; }

.form-columnize, .form-columnize--3 {
	column-gap: $form-gap;

	label {
		@include truncate(100%); }

	@include media('<mob-lg') {
		column-count: 1;
		column-gap: 0; } }

.form-group {
	margin-bottom: $bottom-space--md;

	&:last-child {
		margin-bottom: 0; } }

.form-group-title {
	@include font-bold;
	@include font(18px, round-float(32/18, 2));
	margin-bottom: $bottom-space; }

/* form layout end */

/* form footer */
.form-footer {
	@include clear;

	.form-buttons {
		float: left; }

	.form-note {
		display: block;
		overflow: hidden;
		@include font(14px, 22px);
		min-height: 50px; } }

/* form footer end */

/* form common */
input[type="text"], input[type="password"], input[type="email"], input[type="search"], textarea {
	display: block;
	//+display-inline-block-top
	padding: #{$form-input-space-vt} #{$form-input-space-h} #{$form-input-space-vb};
	@include size(100%, $form-input-height);
	@if $form-letter-spacing {
		letter-spacing: $form-letter-spacing; }
	background: $form-bg-color;
	box-shadow: none;
	border: #{$form-border-width} solid #{$form-border-color};
	cursor: pointer;

	@if $animation-use {
		transition: {
			property: border-color, background-color, box-shadow;
			duration: $animation-speed; } }

	@at-root .select2, .#{$user-content} select, .select, & {
		margin: 0 0 $form-bottom-space; }

	@at-root .select2-container--default .select2-selection .select2-selection__rendered, .#{$user-content} select, .select, & {
		@include font-alt;
		@include font($form-font-size, $form-line-height);
		color: $form-text-color; } }

textarea {
	padding: #{$form-input-space-vt} #{$form-input-space-h} #{$form-input-space-vb};
	min-height: 114px;
	height: 114px;
	resize: vertical; }

input[type="text"], input[type="password"], input[type="email"], input[type="search"], textarea, select, .select, .select2 {

	.input-wrap &, &:last-child {
		margin-bottom: 0; } }

.error-note {
	float: left;
	position: relative;
	@include size(100%, 22px);
	@include font(12px, 22px);
	letter-spacing: normal; // Важно!
	@include truncate(none);
	color: $form-error-color;
	display: none;

	margin-top: -#{$form-bottom-space};

	.input-wrap &, .select & {
		float: none;
		position: absolute;
		margin-top: 0; }

	// нужно выбрать только ближайший элемент ошибки формы (нельзя использовать ~)
	form .error + &,
	form .error + .select2 + & {
		display: block; } }

.success-note {

	$this-icon-size: 16px;
	$this-icon-lines-width: 11px;
	$this-icon-lines-height: 3px;
	$this-icon-lines-color: $form-success-color;

	float: left;
	position: relative;
	left: 100%;
	top: round(-$form-input-height*0.5 - $this-icon-size*0.5);
	margin-top: -#{$form-bottom-space};
	margin-left: 4px;
	@include size($this-icon-size);
	@include font(0, 0);
	letter-spacing: 0;
	backface-visibility: hidden;
	//z-index: -1
	pointer-events: none;
	display: none;

	.input-wrap &, .select & {
		float: none;
		position: absolute;
		top: round($form-input-height*0.5);
		margin: -#{round($this-icon-size*0.5)} 0 0 #{round(($text-layout-space - $this-icon-size)*0.1)}; }

	@include media('<tab-lg') {
		margin-left: #{round(($text-layout-space--md - $this-icon-size)*0.1)}; }

	@include media('<tab-sm') {
		margin-left: #{round(($text-layout-space--sm - $this-icon-size)*0.1)}; }

	// нужно выбрать тольк ближайший элемент подтверждения формы ((нельзя использовать ~))
	form .success + .error-note + &,
	form .success + .select2 + .error-note + & {
		display: block; }

	&::before, &::after {
		@include pseudo();
		bottom: 0;
		backface-visibility: hidden;
		background-color: $this-icon-lines-color;

		@if $animation-use {
			transition: {
				property: background-color;
				duration: $animation-speed; } } }

	&::before {
		@include size(round($this-icon-lines-width*0.75), $this-icon-lines-height);
		right: 50%;
		transform-origin: bottom right;
		transform: rotate(45deg); }

	&::after {
		@include size(round($this-icon-lines-width*1.3), $this-icon-lines-height);
		left: 50%;
		transform-origin: bottom left;
		transform: rotate(-45deg); } }

/* form common end */

/* form placeholder */
input, textarea {
	@include placeholder {
		color: $form-placeholder-color;
		transition: $animation-speed opacity; }

	//form placeholder hide on focus
	@include placeholder-focus {
		opacity: 0; } }

/* form placeholder end */

/* form checkbox and radiobutton */
input[type="checkbox"] ~ label, input[type="radio"] ~ label, .check-label, .radio-label {
	@include display-inline-block-top;
	@include font-normal;
	@include font(16px, 22px);
	letter-spacing: 0.025em;
	color: $text-color;
	text-transform: none;
	cursor: pointer;
	position: relative;
	user-select: none;
	white-space: normal !important;
	text-overflow: clip !important;

	@if $animation-use {
		transition: {
			property: color;
			duration: $animation-speed; } }

	// hover
	.no-touchevents &:hover {
		color: $checkbox-color;

		input[type="checkbox"]:not(:disabled) ~ i {
			&::before {
				border-color: $checkbox-color; } } } }

input[type="checkbox"],
input[type="radio"] {
	float: left;
	@include size(16px);
	padding: 0;
	margin: 0;
	display: none;

	& ~ label > span,
	& ~ span {
		display: inline-block;
		vertical-align: top;
		position: relative;
		padding: 0 0 0 34px;
		z-index: 2; }

	& ~ i {
		@include font(0, 0);
		position: absolute;
		left: 0;
		top: 50%; } }

.no-touchevents .check-label:hover {

	input[type="checkbox"]:not(:disabled) ~ i {
		&::before {
			border-color: $checkbox-color; } } }

.no-touchevents .radio-label:hover {

	input[type="radio"]:not(:disabled) ~ span {
		&::before {
			border-color: $checkbox-color; } } }

//checkbox
input[type="checkbox"] {

	& ~ label,
	& ~ i {
		&::before,
		&::after {
			content: '';
			@include size(22px);
			position: absolute;
			left: 0;
			top: 50%;
			margin-top: -11px;
			border-radius: 0;

			@if $animation-use {
				transition: {
					property: background-color, border-color;
					duration: $animation-speed; } } }

		&::before {
			border: 2px solid $checkbox-color;
			background-color: transparent; }

		&::after {
			content: none; } }

	& ~ label > span,
	& ~ span {
		&::before,
		&::after {
			content: '';
			position: absolute;
			@include size(0, 4px);
			left: 0;
			top: 50%;
			border-radius: 0;
			background: white;

			@if $animation-use {
				transition: {
					property: opacity;
					duration: $animation-speed;
					timing-function: linear; } } }

		&::before {
			transform: rotate(45deg);
			transform-origin: 0 10px;
			z-index: 1; }

		&::after {
			transform: rotate(-50deg);
			transform-origin: 0 10px;
			left: 22px; } }

	// checkbox checked
	&:checked {
		& ~ label,
		& ~ i {
			&::before {
				border-color: $checkbox-color;
				background-color: $checkbox-color; } }

		& ~ label > span,
		& ~ span {
			&::before {
				width: 7px;
				margin: -5px 0 0 0;
				backface-visibility: hidden;

				@if $animation-use {
					transition: {
						property: width;
						duration: 0.1s; } } }

			&::after {
				width: 11px;
				margin: 0 0 0 -7px;
				backface-visibility: hidden;

				@if $animation-use {
					transition: {
						property: width;
						duration: 0.1s;
						delay: 0.1s; } } } } }

	// checkbox disabled
	&:disabled {
		& ~ label,
		& ~ span {
			color: #bdbdbd !important;
			cursor: default; }

		& ~ label,
		& ~ i {
			&::before {
				border-color: #d6d6d6;
				background-color: #f3f3f3; } }

		& ~ label > span,
		& ~ span {
			&:before,
			&:after {
				background: #d6d6d6; } } } }

// radio
input[type="radio"] {

	& ~ label,
	& ~ span {
		&::before, &::after {
			content: '';
			@include size(22px);
			position: absolute;
			left: 0;
			top: 50%;
			margin-top: -11px;
			border-radius: 50px; }

		&::before {
			border: 2px solid $checkbox-color;

			@if $animation-use {
				transition: {
					property: background-color, border-color;
					duration: $animation-speed; } } }

		&::after {
			background: none;

			@if $animation-use {
				transition: {
					property: opacity;
					duration: 0.2s; } } } }

	& ~ label > span,
	& ~ i {
		&::after {
			content: '';
			@include size(12px);
			position: absolute;
			top: 50%;
			left: 5px;
			margin-top: -6px;
			border-radius: 50%;
			overflow: hidden;
			background: $checkbox-color;
			animation: bounceout 0s;
			animation-iteration-count: 1;
			animation-fill-mode: forwards;
			opacity: 0; } }

	& ~ i {
		z-index: 3; }

	// radio checked
	&:checked {
		& ~ label,
		& ~ span {
			&::before {
				border-color: $checkbox-color; } }

		& ~ label > span,
		& ~ i {
			&::after {
				animation: bounce 0.3s;
				animation-iteration-count: 1;
				opacity: 1;
				background-color: $checkbox-color; } } }

	@keyframes bounce {
		0% {
			transform: scale(0.8);
			opacity: 0.4; }

		50% {
			transform: scale(1.4);
			opacity: 1; }

		100% {
			transform: scale(1);
			opacity: 1; } }

	@keyframes bounceout {
		0% {
			transform: scale(0);
			opacity: 1; }

		50% {
			transform: scale(1.4);
			opacity: 1; }

		100% {
			transform: scale(0);
			opacity: 0.4; } }

	// radio disabled
	&:disabled {
		& ~ label,
		& ~ label > span,
		& ~ span {
			color: #bdbdbd;
			cursor: default;

			&::before {
				border-color: #d6d6d6;
				background-color: #f3f3f3; } }

		& ~ label > span,
		& ~ i {
			&::after {
				background: #b9b9b9; } } } }

.check-list {
	@include font(16px, 20px);

	&, .#{$user-content} & {
		margin: 0;
		padding: 0;

		li {
			margin: 0 0 20px;
			padding-left: 0;

			&::before {
				content: none; } } } }

/* form checkbox and radiobutton end */


/* select */
// select overall
.#{$user-content} {
	select {
		display: block;
		width: 100%; } }

.select {
	position: relative;
	width: 100%;

	.input-wrap & {
		margin-bottom: 0; }

	.cselect {
		width: 100%; } }

.#{$user-content} select,
.select select,
.select2-container--default .select2-selection {
	display: block;
	position: relative;
	overflow: hidden;
	height: $form-input-height;
	width: 100%;
	cursor: pointer;
	padding: #{$form-input-space-vt} #{$select-arrow-width} #{$form-input-space-vb} #{$form-input-space-h};
	white-space: nowrap;
	text-indent: 0;
	text-overflow: '';
	border: #{$form-border-width} solid #{$form-border-color};
	border-radius: 0;
	background: $form-bg-color;
	box-shadow: none;
	outline: none;

	@if $animation-use {
		transition: {
			property: background, border, color, box-shadow;
			duration: $animation-speed; } } }

//.#{$user-content} select,
//.select select,
//.select2-container--default .select2-selection .select2-selection__rendered
//include font-normal
//include font($form-font-size, $form-line-height)
//ext-transform: none
//olor: $form-text-color

.select {
	select, .select2 {
		margin-bottom: 0; }

	select {
		padding-right: 40px; } }

//&:focus
//olor: $form-text-color
//order-color: $form-border-focus-color

.select,
.select2-container--default .select2-selection--single .select2-selection__arrow b {
	&::before, &::after {
		content: '';
		position: absolute;
		right: 22px;
		top: 50%;
		@include size(8px, 2px);
		background-color: $text-color-base;
		margin-top: -1px;
		z-index: 1; }

	&::before {
		transform: rotate(45deg); }

	&::after {
		transform: rotate(-45deg);
		right: 17px; } }

.select2-container--default {
	.select2-selection--single {
		.select2-selection__rendered {
			padding-left: 0;
			padding-right: 0; }

		.select2-selection__arrow {
			position: absolute;
			right: 0;
			top: 0;
			@include size($select-arrow-width, $select-arrow-height);
			background-color: $form-bg-color;
			z-index: 2;

			b {
				border: none;
				@include cover();
				margin: 0; } } }

	.select2-search--inline .select2-search__field {
		height: auto;
		margin: 0; }

	// multiple
	.select2-selection--multiple {
		height: auto;
		padding: #{$form-input-space-vt - $multiple-item-space} #{$select-space-h} #{$form-input-space-vb - $multiple-item-space};
		//if has arrow
		//padding: #{$form-input-space-vt - $multiple-item-space} #{$select-arrow-width} #{$form-input-space-vb - $multiple-item-space} #{$select-space-h}

		.select2-selection__rendered {
			padding-left: 0;
			padding-right: 0;
			margin: 0;
			vertical-align: top;
			max-width: none;

			li {
				padding: 0 4px;
				margin: #{$multiple-item-space} 8px #{$multiple-item-space} 0;
				border: none;

				&::before {
					content: none; } } }

		.select2-selection__choice {
			background-color: #f3f3f3;
			border: 1px solid #f3f3f3;
			border-radius: 0;
			color: $text-color; }

		.select2-selection__choice__remove {
			color: $text-color;

			&:hover {
				color: $link-color; } } }

	.select2-selection--single,
	.select2-selection--multiple {
		.select2-selection__placeholder {
			color: $form-placeholder-color; } }

	&.select2-container--focus, &.select2-container--open {
		.select2-selection--multiple, .select2-selection--single {
			border: #{$form-border-width} solid #{$form-border-focus-color}; } }

	.select2-search--dropdown {
		.select2-search__field {
			margin: 0;
			height: 34px;
			padding: 4px;
			border-color: $form-border-color;
			border-width: 1px;
			line-height: 24px;

			&:focus {
				border-color: $form-border-focus-color;
				//box-shadow: inset 0 0 0 1px $form-border-focus-color
				box-shadow: none; } } }

	.select2-results>.select2-results__options {
		max-height: 200px; }

	.select2-results__option[aria-selected=true] {
		background-color: #f3f3f3;
		color: $text-color; }

	.select2-results__option--highlighted[aria-selected] {
		background-color: $base-color;
		color: $bg-color-base; } }

// dropdown
.select2-dropdown {
	border: #{$form-border-width} solid #{$form-border-focus-color};
	border-radius: 0;
	z-index: 9999; }

.select2-results__options {
	@include font-normal;
	@include font($form-font-size, $form-line-height); }

.select2-results__option {
	padding: 8px #{$select-space-h} 8px #{$select-space-h}; }

