/* inputs states */
@mixin form-success() {
	color: $form-success-color;
	border-color: $form-success-color; }

@mixin form-error() {
	color: $form-error-color;
	border-color: $form-error-color; }

@mixin form-has-value() {
	color: blueviolet;
	border-color: blueviolet; }

@mixin form-on-focus() {
	color: blue;
	border-color: blue; }

input[type="text"], input[type="password"], input[type="email"], input[type="search"], textarea, .#{$user-content} select, .select {

	//form hover
	&:hover {
		border-color: $form-border-focus-color; }

	//form focus
	&:focus {
		border-color: $form-border-focus-color;
		cursor: text; }

	// has value (js)
	@at-root label, & {
		&.input--has-value {
			@include form-has-value; } }

	//form success
	@at-root label, & {
		&.success {
			@include form-success; } }

	//form error placeholder
	&.success {
		@include placeholder {
			color: $form-success-color; } }

	//form error
	@at-root label, & {
		&.error {
			@include form-error; } }

	//form error placeholder
	&.error {
		@include placeholder {
			color: $form-error-color; } }

	// on focus (js)
	@at-root label, & {
		&.input--focus {
			@include form-on-focus; } } }

.select2 {
	.select2-selection, .select2-selection__rendered, .select2-selection__placeholder {
		//color: goldenrod
		//border-color: goldenrod

		.input--has-value + & {
			@include form-has-value; }

		.success + & {
			@include form-success; }

		.error + & {
			@include form-error; }

		.input--focus + & {
			@include form-on-focus; } }

	.select2-search__field {
		.success + & {
			@include placeholder {
				color: $form-success-color; } }

		.error + & {
			@include placeholder {
				color: $form-error-color; } } } }

/* inputs states end */
