@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
	font-size: 62.5%;

	// блокировка скролла
	&.css-scroll-fixed {
		@include scroll-blocked(); } }

body {
	position: relative;
	@include font-normal;
	@include font($font-size, $line-height);
	color: $text-color;
	height: 100%;
	min-width: 320px;
	background: $bg-color url(../../img/empty.png) no-repeat left top fixed;

	// выделение текста
	*::selection {
		background-color: $base-color;
		color: white; } }

h1, h2, h3, h4, h5, h6 {
	padding: 0;
	@include font-alt-bold;
	color: $title-color;
	margin: 0 0 $bottom-space;

	@include media('<mob-lg') {
		margin-bottom: round-float($bottom-space*0.85, 1); }

	&:last-child {
		margin-bottom: 0; }

	a {
		text-decoration: none;
		color: inherit;

		.no-touchevents &:hover {
			color: $base-color;
			text-decoration: underline; } } }

h1 {
	@include font($font-size-h1, $line-height-h1);

	@include media('<mob-lg') {
		@include font($font-size-h1--sm, $line-height-h1--sm); } }

h2 {
	@include font($font-size-h2, $line-height-h2);

	@include media('<mob-lg') {
		@include font($font-size-h2--sm, $line-height-h2--sm); } }

h3 {
	@include font($font-size-h3, $line-height-h3); }

h4 {
	@include font($font-size-h4, $line-height-h4); }

h5 {
	@include font($font-size-h5, $line-height-h5); }

h6 {
	@include font($font-size-h6, $line-height-h6);
	text-transform: uppercase; }

p {
	padding: 0;
	margin: 0 0 $bottom-space; }

hr {
	line-height: 1px;
	height: 0;
	border: none;
	background: none;
	border-bottom: 1px solid $border-color;
	margin-bottom: $bottom-space; }

a {
	color: $link-color;
	text-decoration: underline;
	cursor: default;
	//outline: none

	&:link {
		cursor: pointer; }

	.no-touchevents &:hover {
		text-decoration: none; }

	&[href^="tel"], .no-touchevents &[href^="tel"]:hover {
		text-decoration: none !important;
		color: inherit;
		cursor: default; }

	&[href^="javascript:"] {
		cursor: default; } }

img {
	border: none;
	vertical-align: top;
	max-width: 100%;

	table & {
		max-width: none; } }

b, strong, th {
	@include font-bold; }

i, em {
	@include font-italic; }

i b, i strong, em b, em strong, b i, b em, strong i, strong em {
	@include font-bold-italic; }

.entry-text {
	@include font-bold;
	font-size: 1.2em;
	margin-bottom: $bottom-space;

	b, strong {
		@include font-bold; }

	i, em {
		@include font-bold-italic; } }

.entry-img {
	position: relative;
	overflow: hidden;
	@include ratio(10, 6);
	margin-bottom: $bottom-space;

	@include media('<mob-md') {
		@include ratio(16, 9); }

	img {
		position: absolute;
		left: 50%;
		top: 50%;
		max-width: 100%;
		max-height: none;
		min-width: 0;
		min-height: 0;
		text-align: left;
		width: calc(100% + 4px);
		height: calc(100% + 4px);
		transform: translate(-50%, -50%);
		@include object-fit(cover);

		//.no-objectfit &
		//idth: auto
 } }		//eight: 100%

.entry-img__title {
	position: absolute;
	@include position(50%);
	@include padding-horizontal(0 !important);
	margin: 0;
	transform: translate(-50%, -50%);
	z-index: 10;
	@include font($font-size-h1, $line-height-h1);
	color: $bg-color-base;
	text-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
	text-align: center;

	@include media('<desk-md') {
		@include font($font-size-h1--md, $line-height-h1--md); }

	@include media('<mob-lg') {
		@include font($font-size-h1--sm, $line-height-h1--sm); }

	@include media('<mob-md') {
		@include font(22px, 1); } }

.mark {
	color: $mark-color;
	font-style: inherit;
	font-weight: inherit; }

.text-color-alt {
	color: $base-color; }

.align-center {
	text-align: center; }

.align-left {
	text-align: left; }

.align-right {
	text-align: right; }

.float-left {
	float: left; }

.float-right {
	float: right; }

.nobr {
	white-space: nowrap; }

.hide-text {
	@include hide-text; }

/* content img */

.layout-article {
	& > img, .imgc {
		text-align: center;
		display: block;
		margin: 0 auto $bottom-space;
		width: $flood-layout-width;
		max-width: 100%;
		clear: both; }

	.imgl {
		text-align: left;
		float: left;
		margin: 0 $bottom-space $bottom-space 0; }

	.imgr {
		text-align: right;
		float: right;
		margin: 0 0 $bottom-space $bottom-space; }

	.imgl, .imgr {
		max-width: 40%;

		@include media('<tab-sm') {
			float: none;
			display: block;
			margin-left: auto;
			margin-right: auto;
			max-width: 100%; } } }

.imgc, .imgl, .imgr {
	figcaption {
		display: block;
		padding: $bottom-space 0 0;
		margin: 0;
		@include font($font-size--sm, $line-height--sm); } }

/* content img end */

/* columnize */
.columnize, .columnize--3, .columnize--4 {
	column-count: 2;
	column-gap: 2em;
	margin-bottom: $bottom-space;
	font-size: 0.9em; }

.columnize--3 {
	column-count: 3;
	font-size: 0.85em; }

.columnize--4 {
	column-count: 4;
	font-size: 0.8em;

	@include media('<desk-sm') {
		column-count: 3; } }

.columnize--3, .columnize--4 {

	@include media('<tab-lg') {
		column-count: 2; } }

.columnize, .columnize--3, .columnize--4 {

	@include media('<tab-sm') {
		column-count: 1; } }

/* columnize end */

.#{$user-content} {
	@each $tags in h2, h3, h4, h5 {
		p + #{$tags},
		ul + #{$tags},
		ol + #{$tags} {
			margin-top: round-float($bottom-space--md, 1); } } }

/* list */
.#{$user-content} {
	//list
	ul, ol {
		margin: 0 0 $bottom-space;
		padding: 0;
		list-style: none;

		li {
			padding-left: $list-space;
			margin: 0 0 round($bottom-space*0.5);
			position: relative; } }

	ul > li::before {
		content: '';
		display: block;
		float: left;
		@include size(0);
		margin: 0.4em 0 -0.5em -#{$list-space};
		border: solid transparent {
			width: 5px 0 5px 9px;
			left-color: $base-color; } }

	ol {
		counter-reset: section;

		& > li {
			counter-increment: section;
			height: 1%;
			overflow: hidden;

			&::before {
				position: relative;
				display: inline-block;
				text-align: left;
				@include font-alt-bold;
				color: $base-color;
				content: counter(section);
				padding: 0 4px 0 0;
				min-width: $list-space;
				margin-left: -#{$list-space}; } } }

	ul, ol {
		ul, ol {
			margin: round($bottom-space*0.5) 0; } } }

/* list reset */
.reset-list,
.#{$user-content} .reset-list {
	list-style: none;

	li {
		&, &:first-child {
			padding: 0;

			&::before {
				content: none; } } } }

/* list reset end */
/* list end */

/* table */
table {
	border-collapse: collapse;
	border-spacing: 0;
	width: auto;

	td, th {
		@include font($font-size, $line-height);
		color: inherit;
		padding: 8px 15px;
		border-color: $text-color-base; } }

.table-auto {
	position: relative;
	overflow: auto;
	max-width: 100%;
	-webkit-overflow-scrolling: touch;
	margin-bottom: $bottom-space;

	table {
		margin-bottom: 0;
		width: auto;
		min-width: 100%; } }

.#{$user-content} {
	table {
		margin-bottom: $bottom-space; }

	.table-auto table {
		margin-bottom: 0; }

	.tbl {
		border: none;

		td, th {
			color: $text-color;
			background-color: $bg-color-base;
			border: 0 solid $base-color;
			@include font(1.4rem, 1.5);

			&:first-child, &:first-child {
				border-left-width: 1px; }

			&:last-child, &:last-child {
				border-right-width: 1px; } }

		tr:last-child {
			td {
				border-bottom-width: 0; } }

		td {
			border-top-width: 1px; }

		tbody tr:first-child td {
			border-top-width: 0; }

		tbody tr:nth-child(even) td {
			background: $base-color;
			color: $bg-color-base; }

		th, thead td {
			@include font-bold;
			font-size: 1.6rem;
			text-align: left; }

		th, thead td, tfoot td {
			background: $base-color;
			color: $bg-color-base;
			border-color: $base-color;
			border-top-width: 0; } } }

/* table end */

/* blockquote */
.#{$user-content} {

	blockquote {
		display: block;
		margin: 0 0 $bottom-space;
		padding: 0;
		background-color: white;

		q {
			display: block;
			margin: 0;
			padding: 0 0 10px;
			font-family: $font-alt;
			@include font(round-float($font-size*1.25, 1), $line-height);
			color: $text-color;
			text-align: center;

			&::before, &::after {
				@include pseudo($pos: relative);
				@include font(4rem, 1);
				text-align: left; }

			&::before {
				content: '«';
				padding-left: 17%;
				padding-bottom: 10px; }

			&::after {
				content: '»';
				text-align: right;
				padding-right: 18.5%;
				padding-top: 8px; } }

		cite {
			text-align: right;
			display: block;
			margin-left: auto;
			@include font($font-size, $line-height);
			color: $text-color-light; } } }

/* blockquote end */

/* user spacer */
.user-spacer {
	display: block;
	height: $bottom-space--lg;
	@include font(0, 0);
	@include clear;

	@include media('<desk-lg') {
		height: $bottom-space--ml; } }

/* user spacer end */

/* datetime */
.datetime {
	display: block;
	font-style: normal;
	margin-bottom: $bottom-space; }

/* datetime end */

/* view print */
.view-print {
	@include display-inline-block-top;
	padding: 6px 12px;
	margin-bottom: $bottom-space;
	background: #f9f9f9;
	line-height: 1;
	color: black;
	text-decoration: none;

	@if $animation-use {
		transition: {
			property: background-color;
			duration: $animation-speed; } }

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	@include media('<tab-lg') {
		display: none; }

	&:last-child {
		margin-bottom: 0; }

	.no-touchevents &:hover {
		background-color: #eaeaea; }

	svg {
		display: inline-block;
		vertical-align: middle;
		width: 20px;
		height: 20px;
		margin-right: 5px; }

	@if $animation-use {
		path {
			transition: {
				property: fill;
				duration: $animation-speed; } } }

	span {
		display: inline-block;
		vertical-align: middle; } }

/* view print end */
