@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* user form */
.form-title {
	display: block;
	@include font-alt-bold;
	@include font($font-size-h2, $line-height-h2);
	color: $title-color;
	text-align: center;
	margin-bottom: $bottom-space--md; }

.user-form {
	background: $bg-color-alt;
	@include padding-vertical($text-layout-space);
	margin-bottom: $bottom-space;
	border: solid $border-color;
	border-width: 1px 0;

	&:last-child {
		margin-bottom: 0; }

	@include media('<tab-lg') {
		@include padding-vertical($text-layout-space--md); }

	@include media('<mob-lg') {
		@include padding-vertical($text-layout-space--sm); }

	form {
		display: block; }

	.input-wrap {
		margin-bottom: 50px; }

	textarea {
		height: 130px;
		max-height: 130px;
		resize: none; }

	.captcha {
		@include display-inline-block-top;
		@include clear-spacing;
		position: relative; // Важно! Для позицирования отметки подтверждения полей формы
		z-index: 1; // Важно! Отметка подтверждения полей формы имеет z-index: -1

		@include clear;

		img {
			$margin-right: 20px;
			display: inline-block;
			vertical-align: middle;
			max-width: calc(50%);
			margin-right: $margin-right;

			& + input {
				display: inline-block;
				vertical-align: middle;
				width: 200px; } }

		@include media('<tab-lg') {
			img {
				display: block;
				max-width: none;
				margin-bottom: $bottom-space--sm;

				& + input {
					display: block;
					max-width: none;
					width: 100%; } } } }

	.form-footer {
		@include clear;

		@include media('<mob-lg') {
			padding-top: $bottom-space--sm; } }

	.form-buttons {
		float: left;
		margin-right: 30px;

		@include media('<mob-lg') {
			float: none;
			margin: 0; }

		.btn-submit {
			width: 200px; } }

	.form-note {
		overflow: hidden;
		min-height: 50px;
		@include clear-spacing;
		line-height: 25px;

		&::before {
			content: '';
			display: inline-block;
			vertical-align: middle;
			min-height: inherit; } }

	.form-note__item {
		display: inline-block;
		vertical-align: middle;
		width: calc(100% - 6px);
		@include font($font-size--sm, $line-height--sm);
		letter-spacing: normal; } }

/* user form end */

/* slick sliders */
// arrows
.slick-arrow {
	@include size(40px);
	display: block;
	position: absolute;
	border: none;
	border-radius: 0;
	background: none;
	outline: none;
	box-shadow: none;
	opacity: 1;
	z-index: 6;
	transition-property: background, opacity, visibility;
	transition-duration: 0.2s;

	.no-touchevents &:hover {
		opacity: 1;
		background: #e6e6e6; }

	&.slick-disabled, .no-touchevents &.slick-disabled {
		opacity: 0.25;
		-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		cursor: default;

		&::before {
			opacity: 1; } }

	&::before {
		content: none; }

	&::after {
		top: 50%;
		content: "";
		position: absolute;
		opacity: 1;
		pointer-events: none;
		@include size(12px);
		transform: rotate(45deg);
		border: 0 solid #000; } }

.slick-prev {
	left: 0;

	&::after {
		left: 50%;
		border-width: 0 0 2px 2px;
		margin: -6px 0 0 -4px; } }

.slick-next {
	right: 0;

	&::after {
		right: 50%;
		border-width: 2px 2px 0 0;
		margin: -6px -4px 0 0; } }

// dots
.slick-dots {
	margin: 0;
	line-height: 0;

	.#{$user-content} & {
		margin: 0;
		max-width: none; }

	li {
		vertical-align: top;
		width: auto;
		height: auto;
		margin: 0;

		&, .#{$user-content} & {
			padding: 4px 10px;
			margin: 0;

			&::before {
				content: none; } }

		button {
			width: 12px;
			height: 12px;
			padding: 0;
			border-radius: 50%;
			background: $base-color;
			position: relative;
			transition-property: background, border, box-shadow, transform;
			transition-duration: 0.6s;

			&::before {
				content: '';
				position: absolute;
				left: 2px;
				top: 2px;
				height: 8px;
				width: 8px;
				background: #fff;
				border-radius: 50%;
				opacity: 1;
				box-shadow: 0 2px 3px rgba(0, 0, 0, 0.20);
				transition-property: transform, opacity;
				transition-duration: $animation-speed; } }

		&.slick-active button::before {
			opacity: 0; } } }

//.no-touchevents &:hover button::before
/* slick sliders end */

/* images slider */
.images-slider {
	position: relative;
	margin-bottom: $bottom-space;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--lg; } }

.images-slider__list, .slick-dotted.images-slider__list {
	padding-bottom: $bottom-space;
	margin-bottom: 0;
	position: relative;
	z-index: 2; }

.images-slider__item {
	float: none;
	display: inline-block;
	vertical-align: top;
	opacity: 0;
	white-space: normal;
	overflow: hidden;
	position: relative;
	background: #494949;

	.slick-initialized & {
		display: block;
		float: left;
		opacity: 1; }

	.no-js & {
		display: block;
		width: 100%;
		opacity: 1; }

	&::before {
		content: '';
		display: block;
		@include ratio(16, 9); }

	img {
		position: absolute;
		@include position(0);
		text-align: center;
		max-width: none;
		width: 100%;
		height: 100%;
		@include object-fit(contain); } }

.images-slider {

	.slick-arrow {
		right: 0;
		left: auto;
		top: 100%;
		transform: translate(0, 0); }

	.slick-prev {
		right: 40px; }

	// slider counter
	.slider-counter {
		position: absolute;
		left: 0;
		top: 100%;
		color: $text-color-light;

		.slide-curr {
			color: $text-color; } }

	.slick-dots, .#{$user-content} & .slick-dots {
		position: absolute;
		bottom: $bottom-space + 1;

		li {
			@include padding-horizontal(7px);

			button {
				width: 6px;
				height: 6px;
				border-radius: 50%;
				background-color: white;
				opacity: 0.5;
				transition-property: opacity, transform;
				transition-duration: $animation-speed;

				&::before {
					content: none; } }

			&.slick-active button {
				opacity: 1; }

			.no-touchevents &:hover button {
				opacity: 1;
				transform: scale(2); } } } }

/* images slider end */

/* jFiler */
.upload-file {
	opacity: 0; }

.jFiler {
	position: relative; }

.jFiler-input-dragDrop {
	margin: 0;
	cursor: pointer;
	padding: 20px;
	background: $bg-color-alt;
	border: 1px dashed $border-color;
	@if $animation-use {
		transition: {
			property: background-color, border-color, transform;
			duration: $animation-speed; } }
	@else {
		transition: none; }

	.no-touchevents &:hover {
		border-color: $form-border-focus-color; }

	&.dragged .jFiler-input-icon i {
		transform: translate(0, -8px); }

	.jFiler-input-icon {
		margin-top: 0;
		margin-bottom: 15px;
		text-align: center;

		i {
			display: inline-block;
			vertical-align: top;
			background: url(../../img/attach.png) no-repeat;
			background-size: contain;
			@include size(60px, 44px);
			transition-property: background-color, border-color, transform;
			transition-duration: $animation-speed; } }

	.jFiler-input-text strong {
		display: block;
		@include font-normal;
		@include font($font-size--less, $line-height--less);
		color: $text-color;
		text-align: center; } }

.jFiler-items {
	//margin: #{$form-bottom-space} 0 0
	margin: 50px 0 0; }

.jFiler-items-list, .#{$user-content} .jFiler-items-list {
	margin: #{$bottom-space--sm} 0;

	li {
		padding-left: 0;

		&::before {
			content: none; } } }

.jFiler-item {
	margin: #{$bottom-space--sm} 0;
	@include font($font-size--sm, $line-height--sm);
	color: $text-color;
	overflow: hidden; }

.jFiler-item-icon {
	display: none; }

.jFiler-item-title {
	display: inline-block;
	vertical-align: top;
	@include font-bold;
	margin-right: 10px; }

.jFiler-item-others {
	display: inline-block;
	vertical-align: top;
	color: $text-color-light;
	position: relative;

	&::after {
		content: '';
		position: absolute;
		left: 100%;
		bottom: 0;
		@include size(999px, 0);
		border-bottom: 1px dotted $border-color;
		margin: 0 0 3px; }

	span {
		padding-right: 5px; } }

.jFiler-item-assets {
	position: absolute;
	right: 0;
	bottom: 0; }

.jFiler-item-assets ul,
.#{$user-content} ul .jFiler-item-assets ul {
	margin: 0;

	li {
		margin: 0;
		padding: 0 0 3px 8px !important;
		background: $bg-color-alt; } }

.jFiler-item-trash-action {
	display: block;
	position: relative;
	@include size(20px);
	z-index: 2;
	cursor: pointer;
	background-color: $text-color;

	&::before, &::after {
		content: '';
		position: absolute;
		left: 50%;
		top: 50%;
		@include size(16px, 2px);
		border-radius: 2px;
		background: white;
		margin: -1px 0 0 -8px;

		@if $animation-use {
			transition: {
				property: background-color;
				duration: $animation-speed; } }
		@else {
			transition: none; } }

	&::before {
		transform: rotate(-45deg); }

	&::after {
		transform: rotate(45deg); }

	.no-touchevents &:hover::before,
	.no-touchevents &:hover::after {
		background-color: $base-color; } }

/* jFiler end */

/* flashes */
.flashes {
	padding: 0 90px; }

.flashes__item {
	@include font(1.4rem, 1.5);
	color: $text-color-light;

	& > *:last-child {
		margin-bottom: 0; } }

/* flashes end */

/* breadcrumbs */
.breadcrumbs {
	color: $text-color-light;
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

.breadcrumbs__list {
	&, .#{$user-content} & {
		padding: 0;
		margin: 0;
		list-style: none;

		li {
			padding: 0 6px 0 0;
			margin: 0;

			&::before, &:first-child::before {
				content: none; } } }

	cursor: default;
	overflow: hidden;
	@include font($font-size--sm, $line-height--sm);

	.layout-article & {
		max-width: none; }

	li {
		display: inline;
		position: relative;

		&::after {
			display: inline-block;
			vertical-align: baseline;
			content: '•';
			border: none;
			color: $base-color;
			padding-left: 10px; }

		&:last-child::after {
			content: none; } }

	a {
		padding-top: 0.5em;
		color: $text-color;
		text-decoration: none;
		cursor: pointer;

		@if $animation-use {
			transition: {
				property: color;
				duration: $animation-speed; } }

		span {
			border-bottom: 1px solid rgba(white, 0.3);

			@if $animation-use {
				transition: {
					property: border-color;
					duration: $animation-speed; } } }

		.no-touchevents &:hover {
			color: $base-color;

			span {
				border-color: rgba($base-color, 0.3); } } } }

/* breadcrumbs end */

/* files links */
.file {
	$file-icon-size: 42px;
	@include display-inline-block-top;
	min-height: $file-icon-size;
	padding: 0.5em;
	@include font(16px, 20px);
	color: $text-color;
	text-decoration: none;

	@if $animation-use {
		transition: {
			property: background-color;
			duration: $animation-speed; } }

	@include clear;

	svg {
		float: left;
		@include size($file-icon-size); }

	@if $animation-use {
		path {
			transition: {
				property: fill;
				duration: $animation-speed; } } }

	& > span, & > em {
		display: block;
		margin-left: #{$file-icon-size};
		padding-left: 1em;

		@if $animation-use {
			transition: {
				property: color;
				duration: $animation-speed; } } }

	& > em {
		display: block;
		font-size: 1.4rem;
		color: $text-color-light; }

	.no-touchevents &:hover {
		background-color: $base-color;

		& > em {
			color: $bg-color-base; } } }

.st-bg {
	fill: #e2e5e7; }

.st-corner {
	fill: #b0b7bd; }

.st-shadow {
	fill: #cad1d8; }

.st-text {
	fill: #fff; }

/* files links end */

/* feature list */
.feature-list {
	margin-bottom: $bottom-space--md;

	&:last-child {
		margin-bottom: 0; } }

.feature-list__item {
	margin-bottom: $bottom-space;
	@include clear;

	&:last-child {
		margin-bottom: 0; } }

.feature-list__img {
	float: left;
	width: 25%;
	margin: 0.3em $bottom-space $bottom-space--sm 0;

	@include media("<mob-lg") {
		width: auto;
		float: none;
		margin-right: 0; } }

.feature-list__content {
	overflow: hidden;

	h3, p {
		margin-bottom: $bottom-space--sm;

		&:last-child {
			margin-bottom: 0; } } }

/* feature list end */
