@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* === LAYOUT === */
.max-wrap {
	max-width: $page-layout-width + $page-layout-space*2; }

.layout {
	@include padding-horizontal($page-layout-space);
	@include push-auto;

	@include media('<tab-lg') {
		.main & {
			@include padding-horizontal(0); } } }

// выравнивание текстовых элементов на странице статьи (контент обернут в .layout-article)
// и их максимальная ширина
.layout-article {
	padding: round($text-layout-space) #{$text-layout-space} round($text-layout-space*1.5);

	@include media('<tab-lg') {
		padding: round($text-layout-space--md*1.5) #{$text-layout-space--md} round($text-layout-space--md*2); }

	@include media('<tab-sm') {
		padding: round($text-layout-space--sm*1.5) #{$text-layout-space--sm} round($text-layout-space--sm*2); }

	& > p, & > ul, & > ol, & > h1, & > h2, & > h3, & > h4, & > h5, & > h6, & > hr, & > blockquote, & > .layout-text, .imgc figcaption {
		max-width: $text-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	.layout-flood {
		max-width: $flood-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	// элеметы без отступов по бокам
	// минусовые маржины по горизонтале имеют значение только внутри .layout-article
	.layout-full-width, .layout-wide {

		@include margin-horizontal(-#{$text-layout-space});

		@include media('<tab-lg') {
			@include margin-horizontal(-#{$text-layout-space--md}); }

		@include media('<tab-sm') {
			@include margin-horizontal(-#{$text-layout-space--sm}); } } }

// максимальная ширина текстовых элементов на внутренней (текстовой) странице
.max-wrap-text {
	max-width: #{$text-layout-width + $text-layout-space*2};

	@include media('<tab-lg') {
		max-width: #{$text-layout-width + $text-layout-space--md*2}; }

	@include media('<tab-sm') {
		max-width: #{$text-layout-width + $text-layout-space--sm*2}; } }

// выравнивание текстовых элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-text {
	max-width: #{$text-layout-width + $text-layout-space*2};
	@include padding-horizontal($text-layout-space);
	@include push-auto;

	@include media('<tab-lg') {
		max-width: #{$text-layout-width + $text-layout-space--md*2};
		@include padding-horizontal($text-layout-space--md); }

	@include media('<tab-sm') {
		max-width: #{$text-layout-width + $text-layout-space--sm*2};
		@include padding-horizontal($text-layout-space--sm); } }

// максимальная ширина альтернативных элементов на внутренней (текстовой) странице
.max-wrap-flood {
	max-width: #{$flood-layout-width + $text-layout-space*2};

	@include media('<tab-lg') {
		max-width: #{$flood-layout-width + $text-layout-space--md*2}; }

	@include media('<tab-sm') {
		max-width: #{$flood-layout-width + $text-layout-space--sm*2}; } }

// выравнивание альтернативных элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-flood {
	max-width: #{$flood-layout-width + $text-layout-space*2};
	@include padding-horizontal($text-layout-space);
	@include push-auto;

	@include media('<tab-lg') {
		max-width: #{$flood-layout-width + $text-layout-space--md*2};
		@include padding-horizontal($text-layout-space--md); }

	@include media('<tab-sm') {
		max-width: #{$flood-layout-width + $text-layout-space--sm*2};
		@include padding-horizontal($text-layout-space--sm); }

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

// элеметы с отступами по бокам
.layout-wide {
	@include padding-horizontal($text-layout-space);

	@include media('<tab-lg') {
		@include padding-horizontal($text-layout-space--md); }

	@include media('<tab-sm') {
		@include padding-horizontal($text-layout-space--sm); }

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

// элеметы без отступов по бокам
.layout-full-width {

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

// лэяут страницы
.wrapper {
	margin: 0 auto;
	//min-height: 100%
	//height: auto !important
	//height: 100%
	min-height: 100vh;
	overflow: hidden;
	position: relative; }

/* === LAYOUT end === */

/* === CONTENT === */
.main {
	position: relative;
	clear: both;
	z-index: 4; }

.main__holder {
	width: 100%;
	@include clear; }

.content-wrap {
	float: left;
	width: 100%; }

.content {
	margin: 0 200px 0 300px;
	min-height: 50px;

	background-color: whitesmoke;

	@include media('<desk-lg') {
		margin-right: 0; }

	@include media('<tab-lg') {
		margin-left: 0; } }

/* === CONTENT end === */

/* === HEADER === */
.header__holder {
	position: relative;
	padding: 10px 0;
	z-index: 5;
	@include clear;

	background-color: lightgreen; }

/* logo */
.logo {
	position: relative;
	float: left;
	background: url(../../img/logo.png) no-repeat;
	width: 220px;
	height: 85px;
	text-indent: -9999px;
	overflow: hidden;
	margin: 0 2rem 0 0;
	z-index: 5;

	a, & > span {
		display: block;
		height: 100%; } }

/* logo end */
/* === HEADER end === */

/* === NAVIGATION === */
.nav {
	position: relative; }

.nav__list {
	@include clear;

	li {
		position: relative;

		&.has-drop {
			padding-right: 2rem;

			.nav__angle {
				position: absolute;
				right: 0;
				top: 50%;
				transform: translate(0, -50%);
				line-height: 0;
				pointer-events: none; } } }

	a {
		display: block; }

	& > li {
		float: left;
		margin-right: 2rem;

		&.has-drop > .nav__tab .nav__angle i {
			display: inline-block;
			vertical-align: top;
			@include triangle(bottom, tomato, 5px, 2); } } }

.nav__drop {
	position: absolute;
	left: 0;
	top: 100%;
	background-color: white;
	border: 1px solid $border-color;
	padding: 2rem;
	width: 320px;
	opacity: 0;
	visibility: hidden;
	z-index: 10;

	li:hover > & {
		opacity: 1;
		visibility: visible; }

	li {
		margin-bottom: 1rem;
		font-size: 1.4rem;
		line-height: 1.5;

		&.has-drop > .nav__tab .nav__angle i {
			display: inline-block;
			vertical-align: top;
			@include triangle(right, tomato, 5px, 2); } }


	.nav__drop {
		top: 0;
		left: 100%; } }

/* === NAVIGATION end === */

/* === SIDEBAR === */
.sidebar {
	display: inline;
	float: left;
	margin: 0 0 0 -100%;
	width: $sidebar-width;

	background-color: sandybrown;

	@include media('<tab-lg') {
		display: none; } }

/* === SIDEBAR end === */

/* === ASIDE === */
.aside {
	display: inline;
	float: right;
	margin: 0 0 0 -#{$aside-width};
	width: $aside-width;
	background-color: sandybrown;

	@include media('<desk-lg') {
		display: none; } }

/* === ASIDE end === */

/* === FOOTER === */
.footer {
	margin: 0 auto;
	position: relative;
	clear: both;
	z-index: 5;
	@include font(16px, round-float(24/16, 3));
	color: $text-color;
	border-top: 1px solid $border-color;

	background-color: lightgreen; }

.footer__holder {
	overflow: hidden;
	cursor: default;
	padding: $footer-grid-space 0; }

/* footer bottom */
.footer-bottom {
	color: $text-color; }

.footer-bottom__holder {
	position: relative;
	text-align: justify;
	overflow: hidden;
	cursor: default;

	@include clear-spacing;

	&::after {
		content: "1";
		display: inline-block;
		margin-left: 100%;
		height: 1px;
		overflow: hidden; } }

.footer-bottom__col {
	@include display-inline-block-top;
	@include font(16px, round-float(24/16, 3));
	letter-spacing: normal;

	&:last-child {
		text-align: right; }

	a {
		color: $text-color;
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: underline; } } }

/* footer bottom end */

/* developer */
.developer {
	display: inline-block;

	a {
		text-decoration: none;

		.no-touchevents &:hover {
			text-decoration: none; } } }

.developer__logo {
	@include display-inline-block-top;
	position: relative;
	top: 5px;
	padding-right: 3px;
	@include size(154px, 20px);
	text-align: left;
	text-indent: -9999px;

	&::before, &::after {
		@include pseudo();
		@include position(0, 0);
		@include size(149px, 16px);

		@if $animation-use {
			transition: {
				property: opacity, transform, filter;
				duration: 0.2s; } } }

	&::before {
		@include pseudo();
		@include position(4px, 4px);
		background: url('data:image/png;base64,');
		background-size: 2px; }

	//.no-touchevents a:hover
	//ransform: translate(0, 1px)
	//ilter: blur(2px)
	//pacity: 0.6

	&::after {
		@include pseudo();
		background: #00ef85 url('data:image/png;base64,') no-repeat center center;
		background-size: auto 16px;

		.no-touchevents a:hover & {
			transform: translate(0, -1px); } } }

/* developer end */
/* === FOOTER end === */
